body {
  text-align: center;
}

.documents {
  margin-top: 40px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.box {
  border: 1px solid gray;
  margin: 10px;
  padding: 10px 5px;
  border-radius: 10px;
  border-bottom: 3px solid gray;
}

.ellipsis {
  font-size: 0.9em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}

input[type="text"] {
  padding: 5px 10px;
  margin: 10px 5px 10px 0px;
}

.page{
  margin-top: 50px;
}

button {
  background-color: rgb(88, 61, 188);
  color: white;
  border: none;
  padding: 5px;
  border-radius: 10px;
  margin: 0 15px;
}

button:disabled {
  background-color: gray;
}